* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  display: inline-grid;
  width: 80%;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.card {
  margin-top: 10px;
  margin-bottom: 70px;
  width: 200px;
  height: 270px;
  background-color: white;
  width: 100%;
  min-width: 0;
  border-radius: 10px;
  margin-bottom: 70px;
  font-family: "Poppins", sans-serif;
  border: 1px solid black;
}

#dd button:nth-child(3) {
  background: green;
}

.card img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

/* CSS */
.button-31 {
  background-color: rgb(255, 255, 255);
  border-radius: 4px;
  border-style: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: "Farfetch Basis", "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
  margin: 0;
  max-width: none;
  min-height: 44px;
  min-width: 10px;
  outline: none;
  overflow: hidden;
  padding: 9px 20px 8px;
  position: relative;
  text-align: center;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin-left: 10px;
  text-decoration: none;
}

.button-31:hover,
.button-31:focus {
  opacity: 0.75;
}

.button-31 a {
  text-decoration: none;
  color: black;
}

@media only screen and (max-width: 600px) {
  .container {
    grid-template-columns: repeat(1, 1fr);
  }

  .card {
    margin-bottom: 70px;
    margin-top: 30px;
  }
}

@media only screen and (max-width: 900px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
  }

  .card {
    margin-bottom: 70px;
    margin-top: 30px;
  }
}
