* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #f3f2ef;
}

.nav {
  background: linear-gradient(
    90deg,
    rgb(110, 94, 254) 0%,
    rgba(73, 63, 252, 1) 100%
  );
  height: 80px;
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 1.2rem;
}

.logo {
  margin-left: 30px;
  color: white;
}

@media screen and (max-width: 480px) {
  .nav {
    padding: 50px;
    font-size: 1rem;
  }
}
